"use client";

import { useRouter } from "next/navigation";
import Hero from "@/components/landing-new/Hero";
import Manifesto from "@/components/landing-new/Manifesto";
import Features from "@/components/landing-new/Features";
import Stats from "@/components/landing-new/Stats";
import Testimonials from "@/components/landing-new/Testimonials";
import CTA from "@/components/landing-new/CTA";
import FAQ from "@/components/landing-new/FAQ";

export default function Home() {
  const router = useRouter();

  const performSearch = (query: string) => {
    if (query.trim()) {
      router.push(`/search?q=${encodeURIComponent(query)}`);
    }
  };

  return (
    <div className="min-h-screen bg-background text-foreground overflow-x-hidden">
      <nav className="fixed top-0 left-0 right-0 z-50 px-6 py-4 flex items-center justify-between pointer-events-none border-b-2 border-foreground/20 bg-background/95 backdrop-blur-sm">
        <div className="pointer-events-auto">
          <span className="font-display font-black text-3xl tracking-tighter">NEXUS</span>
        </div>
        <div className="pointer-events-auto flex gap-8 font-body font-bold text-sm md:text-base">
          <a href="#" className="hover:text-foreground/50 transition-colors">FEATURES</a>
          <a href="#" className="hover:text-foreground/50 transition-colors">ABOUT</a>
          <a href="#" className="hover:text-foreground/50 transition-colors">LOGIN</a>
        </div>
      </nav>

      <main className="pt-16">
        <Hero onSearch={performSearch} />
        <Manifesto />
        <Stats />
        <Features />
        <Testimonials />
        <CTA />
        <FAQ />
      </main>

      <footer className="py-8 text-center text-foreground/50 font-mono text-sm border-t-4 border-foreground/20 bg-background">
        <p>&copy; 2024 NEXUS SEARCH. Built on principles. Powered by truth.</p>
      </footer>
    </div>
  );
}