export default function Search3DBackground() {
    return (
        <div className="absolute inset-0 -z-10 bg-background overflow-hidden">
            {/* Geometric shapes inspired by brutalist design */}
            <div className="absolute top-0 right-0 w-96 h-96 border-8 border-foreground/10 transform rotate-45 -translate-y-48 -translate-x-48" />
            <div className="absolute bottom-20 left-10 w-64 h-64 border-4 border-foreground/5" />

            {/* Diagonal lines */}
            <div className="absolute inset-0">
                <div className="absolute top-1/4 left-0 right-0 h-px bg-gradient-to-r from-transparent via-foreground/20 to-transparent transform -rotate-12" />
                <div className="absolute top-1/2 left-0 right-0 h-px bg-gradient-to-r from-transparent via-foreground/10 to-transparent transform rotate-12" />
            </div>

            {/* Subtle animated stripes */}
            <svg className="absolute inset-0 w-full h-full opacity-5" preserveAspectRatio="none">
                <defs>
                    <pattern id="stripes" x="0" y="0" width="40" height="40" patternUnits="userSpaceOnUse">
                        <line x1="0" y1="0" x2="40" y2="40" stroke="currentColor" strokeWidth="2" />
                    </pattern>
                </defs>
                <rect width="100%" height="100%" fill="url(#stripes)" />
            </svg>
        </div>
    );
}
